#about {
	@include section; overflow: hidden; position: relative;

	background: url(../img/portfolio-bg.jpg) repeat center ;

	text-align: right;
	& > .container {
		&::before { @include null; display: block; position: absolute; right: 60%; left: -300px;
			background: url(../img/about-bg.jpg) no-repeat center right / 100% auto; top:0; bottom:0;
			mask: linear-gradient(to right, white, white 90%, transparent);
			-webkit-mask: linear-gradient(to right, white, white 90%, transparent);
		}
	}

	.content { display: inline-block; width: 60%; position: relative; z-index: 50; text-align: left;}

	@media screen and (max-width: 960px) {
		& > .container::before { display: none; }
		.content { width: 100%; }
	}

}