#before-after {
	@include section;

	background: url(../img/before-after-bg.jpg) repeat center;

	h2 { @include index-heading-decor; }

	.projects-container { width: 100%; }
	.slick-list { widht: 100%;  }

	@mixin label { @include null; padding: 1rem; display: inline-block; position: absolute; text-transform: uppercase; bottom: 2rem;}

	.project {
		position: relative; display: block;
		white-space: nowrap; font-size:0;

		.before { display: inline-block; width:50%; position: relative; white-space: normal; font-size: 1.5rem; }
		.before:after {@include label; content: 'до'; background-color: #222; color: white; right:0; }
		.after { display: inline-block; width: 50%; position: relative; white-space: normal; font-size: 1.5rem;}
		.after:after {@include label; content: 'после'; background-color: white; color: $color-black; left:0; }
	}

}