#windows-variants {
	@include section;
	background: url(../img/windows-variants-bg.jpg) repeat center ; position: relative;

	& > .container { padding-bottom: 1rem; }

	h2 { @include index-heading-decor; }
	.variants-container { margin-right: -1rem; margin-left: -1rem;}

	&:before { @include null; background: linear-gradient(white, transparent); position: absolute; bottom:0; height: 100px; left:0; right:0; }
}


.window-variant {
	position: relative; text-align: center; margin: 1rem;

	img { display: inline-block !important; margin-bottom: 4rem; filter: drop-shadow(1px 1px 20px  white); }

	.info {
			position: absolute; padding: 2rem; background: white; top:50%; left:100%; min-width: 350px; text-align: left; transform: translateY(-50%); opacity: 0; box-shadow: 3px 3px 5px 0 #0002; z-index: 100; pointer-events: none; margin-left: -10rem;
		.heading { display: flex; flex-flow: row; justify-content: space-between; align-items: center; font-size: 2rem; margin-bottom: 1rem; padding-bottom: 1rem;
		border-bottom: 1px solid #eee; grid-gap: 2rem;}
		.option { display: flex; flex-flow: row; justify-content: space-between; align-items: center; }
	}

	& > .heading { display: inline-block; border: 2px solid #8b8b8b; padding: 2rem 2rem;
		display: inline-flex; flex-flow: row; justify-content: space-between; align-items: center; font-size: 2rem; grid-gap: 2rem;
		h3 { margin-bottom: 0; @include font-light; }
		.price strong { color: $color-yellow; font-weight: bold;}
	}


	&:hover {
		.info { opacity: 1; }
		& > .heading { border-color: $color-yellow; background: white; }
	}
}
