#install {
	@include section; overflow: hidden; position: relative;
	background: url(../img/portfolio-bg.jpg) repeat center; text-align: left; padding-top: 6rem; padding-bottom: 6rem;

	&::before {
		display: inline-block;
		content: '';
		background: linear-gradient(white, transparent);
		position: absolute;
		top: 0;
		height: 100px;
		left: 0;
		right: 0;
	}

	.content { display: inline-block; position: relative; z-index: 50; text-align: left;  }

	.image { display: inline-block; width: 35rem; height: 35rem; border-radius: 50%; overflow: hidden; position: relative; min-width: 35rem; }
	.image img { position: absolute; top:0; bottom:0; left:0; right: 0; object-fit: cover; height: 100%; }

	& > .container {
		display: flex; flex-wrap: nowrap; grid-gap: 4rem; justify-content: space-between; align-items: center;
//			background: url(../img/technology-bg.jpg) no-repeat center left / 100% auto; top:0; bottom:0;
//			mask: linear-gradient(to left, white, white 90%, transparent);
//			-webkit-mask: linear-gradient(to left, white, white 90%, transparent);
	}

	@media screen and (max-width: 960px) {
		.image { display: none; }
		.content { width: 100%; }
	}
}