#footer {
	@include section;

	& > .container {
		display: grid;
		display: grid; grid-auto-rows: min-content; grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
		grid-gap: 1rem;
		padding-bottom: 3rem;
	}

	.wrapper > * { }
	.heading { color: $color-yellow; font-weight: bold; font-size: 1.6rem; padding:0; margin-bottom: 0.5rem; display: flex; flex-flow: row; justify-content: flex-start; align-items: center; }

	.social-icons { grid-row: 2; grid-column: 1; }


	.heading:before { @include null; width: 2.5rem; height: 2.5rem; display: inline-block; margin-right: 1rem; background: no-repeat center / contain;}
	.email .heading:before { background-image: url(../img/icon-contacts-email.svg); }
	.whats-app .heading:before { background-image: url(../img/icon-contacts-email.svg); }
	.phones .heading:before { background-image: url(../img/icon-contacts-phone.svg); }
	.worktime .heading:before { background-image: url(../img/icon-contacts-time.svg); }
	.skype .heading:before { background-image: url(../img/icon-contacts-skype.svg); }

	.developer { background: url(../img/vebas-logo.png) center right no-repeat; padding-right: 145px; min-height: 38px; min-width: 300px; text-align: right; display: flex; align-items: center; max-width: 300px;}

	@media screen and (max-width: 640px) {
		& > .container { display: flex; justify-content: center; flex-wrap: wrap;}
		& > .container > * { width: 80%; max-width: 250px; margin-bottom: 3rem; }

	}
}
