#features {
	@include section;

	h2 { @include index-heading-decor; }


	.features-list {
		display: grid; grid-template-columns: 30% 40% 30%; grid-template-rows: repeat(4,auto);
		&:before { @include null; width: 100%; top: -2rem; bottom: -2rem;
			grid-column: 2; grid-row: 1 / span 3;
			background: url(../img/features-bg-window.png) no-repeat center / contain;
		}
	}


	.feature {
		display: inline-block; font-size: 1.6rem; height: 11rem; position: relative;
		display: inline-flex; flex-flow: column; align-items: flex-start; justify-content: center; padding-left: 12rem;
		padding-bottom: 0.5rem; padding-top: 0.5rem; border-top:1px solid #e0e0e0;
		&:before { @include null; width: 9rem; height:9rem; border-radius: 50%; border: 4px solid $color-yellow;
			position: absolute; left:0; top:50%; transform: translateY(-50%); background: no-repeat center / auto 60%;
		}
		.heading { font-weight: bold; }
	}

	.feature:nth-child(1) { grid-row: 1; grid-column: 1; &:before { background-image: url(../img/icon-features-1.svg); } }
	.feature:nth-child(2) { grid-row: 2; grid-column: 1; &:before { background-image: url(../img/icon-features-2.svg); } }
	.feature:nth-child(3) { grid-row: 3; grid-column: 1; &:before { background-image: url(../img/icon-features-3.svg); } }
	.feature:nth-child(4) { grid-row: 1; grid-column: 3; &:before { background-image: url(../img/icon-features-4.svg); } }
	.feature:nth-child(5) { grid-row: 2; grid-column: 3; &:before { background-image: url(../img/icon-features-5.svg); } }
	.feature:nth-child(6) { grid-row: 3; grid-column: 3; &:before { background-image: url(../img/icon-features-6.svg); } }

	// Media
	@media screen and (max-width: 960px) {
		.feature { height: 9rem; padding-left: 9rem; font-size: 1.4rem; }
		.feature:before { width: 7rem; height:7rem;	}
	}

	@media screen and (max-width: 768px) {
		.features-list { display: block; }
		.feature { width: 49%; border: none; }
		.features-list:before { display: block; position: static; top: auto; bottom: auto; padding-top: 40%; max-width: 300px; margin: 0 auto; margin-bottom: 2rem; }
	}

	@media screen and (max-width: 600px) {
		.feature { width: 100%; display: block; max-width: 400px; margin-left: auto; margin-right: auto; }
	}

}