#article {
	& > .container {}
	article { width: 100%; margin-bottom: 3rem; display: block; position: relative;  }
}

article {

	// Links
	a { color: $color-dark-gray; text-decoration: underline; }
	a:hover  { color: black; text-decoration: none; }

	// Section
	section { margin-bottom: 4rem; }

	// Headers
	h1 { margin-bottom: 0.5em; clear: both; text-align: left; }
	h2 { text-align: left;  margin-top: 2rem;}
	h3 { clear: both; }
	& > h2:first-child { margin-top: 0;}
	h1 + img { margin-top:0; }
	h1 + h2 { margin-top:0; }
	p + img { margin-top: 0;}

	// Images
	img { margin-bottom: 2rem; }


	img.fullwidth { width: 100%; height: auto; float: none; max-width: none; margin-left:0; margin-right: 0; }
	img.float-left { float: left; margin-right: 2rem; max-width: 300px; height: auto; position: relative;  }
	img.float-right { float: right; margin-left: 2rem;  max-width: 300px; height: auto; position: relative; }

	@media screen and (max-width: 640px) {
		& > img, & > p > img { float: none; margin: 0; max-width: none; width: 100%; height: auto; position: static; }
	}

	// Lists
	ul, ol { margin:0; margin-bottom: 2rem; margin-left: 2rem; }
	ul > li { list-style-type: circle; }
	ol > li { list-style-type: decimal; }
	ul > li, ol > li { margin-bottom: 0.5em; margin-left: 2rem; }

	// Article tables
	table {
		width: 100%; border: 2px solid #ccc; margin-bottom: 4rem;
		caption { display: none; }
		td, th { border: 1px solid #d4d4d4; }
		th { background: #eee; text-align: left; padding: 1.5rem; }
		td { padding: 1.5rem; text-align: left; }
		td strong { color: $color-black; font-size: 110%; }
	}
	p { margin-bottom: 1rem;}


	.quote {
		display: inline-block; max-width: 500px; margin-top: 2rem;
		display: flex; justify-content: flex-end;
		border: 1px solid $color-black; padding: 3rem;
	}

}