.breadcrumbs {
	margin-bottom: 3rem !important; position: relative;	min-height:30px; display: flex; flex-flow: row; align-items: center; color: $color-black;

	ul{ margin-left:0 !important;}
	li { display: inline-block; position: relative; }
	li a { display: inline-block; }

	li:not(:last-child):after { @include null;  position: relative; right:0; margin:0 1.5rem; top:50%; transform: translateY(-50%);   display: inline-flex; width:4px; height: 4px; background: $color-yellow; align-content: center; }

	a.B_currentCrumb { color: #858585; }
	a { text-decoration: underline; }
	a:hover { text-decoration: none; }
}
