#how-we-work {
	@include section;

	h2 { @include index-heading-decor; }

	.items-container {
		display: flex; flex-wrap: wrap; margin-left: -2rem; margin-right: -2rem; font-size:0; counter-reset: items; white-space: nowrap;
		justify-content: flex-start; justify-self: center; margin-bottom: -4rem;
		display: grid; grid-auto-rows: min-content; grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
		padding-left: 4rem; padding-right: 4rem;

		background: url(../img/dash-line.png) no-repeat left 80px / 100% 230px;

		.item {
			white-space: normal; display: inline-block; display: inline-flex; flex-flow: column; min-height: 100px;  min-width: 350px; align-items: flex-start; padding: 2rem; padding-left:155px; position: relative; justify-content: center;	margin-bottom: 8rem; font-size: 1.5rem; counter-increment: items; padding-right: 4rem;

			h3 { margin-bottom: 0.5rem; color: $color-yellow;}

			// Icons
			&:before { @include null; position: absolute; left:0; top:50%; transform: translateY(-50%); display: inline-block; width: 145px; min-width: 145px; height: 145px; background: url(../img/how-we-work-1.svg) no-repeat center / 100% auto;  content: counter(items);  font-size: 3rem; display: flex; align-items: center; justify-content: center; color: $color-yellow; font-size: 5rem; font-weight: normal; }
			&:nth-child(2):before { background-image: url(../img/how-we-work-2.svg); }
			&:nth-child(3):before { background-image: url(../img/how-we-work-3.svg); }
			&:nth-child(4):before { background-image: url(../img/how-we-work-4.svg); }
			&:nth-child(5):before { background-image: url(../img/how-we-work-5.svg); }

			// Arrows
			&:not(:last-child)::after { @include null; position: absolute; right:1rem; top: 50%; transform: translateY(-50%); width: 1.5rem; height: 1.5rem; display: block; background: url(../img/icon-arrow-next.svg) no-repeat center / auto 1.5rem; filter: contrast(12%) sepia(100%) brightness(150%) saturate(272%) hue-rotate(8deg);}
		}

		@media screen and (max-width: 1280px) {
			background:
					url(../img/dash-line.png) no-repeat left 80px / 100% 230px,
					url(../img/dash-line.png) no-repeat left 300px / 100% 230px;
		}

		@media screen and (max-width: 960px) {
			background: none;
			.item { padding-left: 120px; margin-bottom: 2rem; min-width: auto; }
			.item:before { width: 100px; height: 100px; min-width: 100px; }
			.item::after { display: none !important; }
		}

	}
}
