#contacts {
	@include section;
	background: url(../img/portfolio-bg.jpg) center repeat; position: relative;


	&:before { @include null; position: absolute; left:0; top:0; bottom:0; right:40%;
		background: url(../img/about-bg.jpg) no-repeat right center  / 100%  ;
		-webkit-mask: linear-gradient(to right, white 80%, transparent);
		mask: linear-gradient(to right, white 80%, transparent);
	}
	text-align: right;
	.contact-form {
		display: inline-block; max-width: 400px; text-align: center;

		h2 { text-align: center; font-size: 3.6rem; display: block; margin-bottom: 3rem; }
		.subheading { font-size: 2rem; margin-bottom: 1em; }
	}


	@media screen and (max-width: 1024px) {
		.contact-form { width: 60%; }
		&:before { right: 50%; }
	}

	@media screen and (max-width: 960px) {
		text-align: center;
		&:before { opacity: 0.3; right: 0; }
		.contact-form { width: auto; max-width: 600px; margin: 0 auto; }
	}



}