#side {
	& > .container { display: block; }
	& > .container > * { margin-bottom: 2rem; }
	.banner { border-right: 1px solid #c0c0c0; }
	.banner img { width: 100%; height: auto; }
}


#side .menu {
	border-right: 1px solid #c0c0c0; background: #f5f5f5;

	h2 { font-size: 2.6rem; @include font-light; background: url(../img/icon-catalog.png) 1rem center no-repeat, $color-white; color: $color-black; padding: 2rem; margin-bottom: 0; border-bottom: 2px solid $color-yellow; padding-left: 4rem;}
	& > ul { margin: 2rem 0 ;}
	ul {
		padding: 2rem; display: block; padding-left: 4rem;
		text-transform: uppercase; font-weight: normal;
		li {
			display: block; padding: 0; padding-left: 1.5rem;  position: relative; margin-bottom: 0.5rem;
			&:before { @include null; width: 5px; height: 5px; background: #bcbcbc; position: absolute; top: 50%; left:0; transform: translateY(-50%); }
			& a { display: block; color: $color-black; text-decoration: none; }
			&.active a { font-weight: bold;}
			&.active:before { background: $color-yellow; }
			& a:hover { text-decoration: none; color: $color-yellow;  }
		}
	}

	@media screen and (max-width: 960px) {


		text-align: center;
		h2 { font-size: 2rem; }
		& > ul { margin: 1rem 0 ;}
		ul { padding: 1rem; }
		ul li { display: inline-block;  padding: 1rem 0.5rem; font-size: 1.4rem; margin-bottom: 0; padding-left: 1rem; margin-right: 1rem; }
	}

}

@media screen and (max-width: 960px) {
	#side .services-menu { display: none; }
}

.ui-menu {

	li { font-size: 1.5rem;  }
	li:not(:last-child) { border-bottom: 1px solid #e0e0e0; }
	li > a { padding: 0.2rem; padding-left: 1rem; text-decoration: none; }

	li.active {
		background: $color-yellow; color: white;
	}

	li.active > a { color: white; }
	li.active > ul { display: block; }

}


