#technology {
	@include section; overflow: hidden; position: relative;
	background: url(../img/portfolio-bg.jpg) repeat center; text-align: left;

	.content { display: inline-block; width: 60%; position: relative; z-index: 50; text-align: left;}

	& > .container {
		&::before { @include null; display: block; position: absolute; left: 60%; right: -300px;
			background: url(../img/technology-bg.jpg) no-repeat center left / 100% auto; top:0; bottom:0;
			mask: linear-gradient(to left, white, white 90%, transparent);
			-webkit-mask: linear-gradient(to left, white, white 90%, transparent);
		}
	}

	@media screen and (max-width: 960px) {
		& > .container::before { display: none; }
		.content { width: 100%; }
	}
}