#faq {
	@include section;
	h2 { @include index-heading-decor; }

	.faq-container {
		font-size: 0;
		display: flex; flex-wrap: wrap; justify-content: center; grid-gap: 2rem; grid-row-gap: 4rem; white-space: nowrap;
		.faq-preview {
			display: inline-block; width: 48%; font-size: 1.5rem; white-space: normal;
			img { display: inline-block; width: 170px; height: 170px; border-radius: 50%; float: left; margin-right: 2rem; transition: .3s all; border: 5px solid transparent; }
			h3 { font-style: 1.8rem; font-weight: bold; margin-bottom: 1rem; }

			.read-more { font-size: 1.3rem;
				display: inline-flex; align-items: center; justify-content: flex-start;
				&:hover { text-decoration: underline; }
				&:before {
					@include null; display: inline-block; width: 1.5rem; height: 1.5rem; margin-right: 0.5rem; background: url(../img/icon-arrow-more.svg) no-repeat center left / 1.5rem auto; filter: contrast(12%) sepia(100%) brightness(150%) saturate(272%) hue-rotate(8deg);
				}
			}

			.content { overflow: hidden; }

			&:hover {
				img { border-color: $color-yellow; }
			}
		}

		@media screen and (max-width: 1024px) {
			.faq-preview { width: 100%; }
		}

		@media screen and (max-width: 500px) {
			.faq-preview img { display: none; }
			.faq-preview { }
		}
	}
}

.faq-list-item {
	h2 { font-size: 2.2rem; font-weight: normal; }
	margin-bottom: 2rem; padding-bottom: 2rem; border-bottom: 1px dashed $color-gray;
}