#navigation {

	@include section(2rem); position: relative; z-index: 50; overflow: visible;
	& > .container  { display: flex; align-items: center; justify-content: center; padding-top: 0;}

	.main-menu {
		font-size: 0; text-align: center;
		li { display: inline-block; position: relative; font-size: 1.6rem; font-weight: bold; text-transform: uppercase; }
		& > li ul {display: none; position: absolute; top: 95%; left:0;  background: white; box-shadow: 0 5px 5px 0 rgba(0,0,0,0.3); border-bottom: 4px solid $color-yellow; z-index: 1000; text-align: left;  }
		& > li ul li { font-size: 1.3rem; padding: 0.5rem; white-space: nowrap; border-bottom: 1px solid #ececec; display: block; transition: .3s all; }
		& > li:hover ul { display: block; }
		& > li ul li a { padding: 0.5rem 1rem; color: $color-black; transition: .3s all;}
		& > li ul li:hover a { padding: 0.5rem 1rem; color: $color-black; color: $color-black; }
		& > li ul li:hover { background: $color-yellow; color: white; }

		li:hover > a { color: $color-yellow; }
		li.active > a { color: $color-yellow; }

		li a { padding: 1.5rem 2rem; display: block; transition: .3s all;}
		& > li:not(:last-child):before { @include null; position: absolute; height: 4px; width: 4px; top: 50%; right: 0; transform: translateY(-50%); background-color: $color-yellow; }


		@media screen and (max-width: 960px) { & > li:hover ul { display: none !important; }	}
	}



}