﻿@font-face {
    font-family: 'Myriad Pro';
    src: url('../fonts/myriad-pro-bold-cond.woff2') format('woff2'),
        url('../fonts/myriad-pro-bold-cond.woff') format('woff'),
        url('../fonts/myriad-pro-bold-cond.ttf') format('truetype'),
        url('../fonts/myriad-pro-bold-cond.svg#myriad-pro-bold-cond') format('svg');
    font-weight: bold;
    font-style: normal;
}

