.message-box  {
	background: white; position: relative; border: 2px solid #fec127; display: flex; justify-content: flex-start; align-items: flex-start; padding: 2rem 4rem; padding-left: 16rem !important; text-align: left; margin: 2rem 0; flex-flow: column;

	h2 { text-align: left; font-size: 2.4rem; margin-bottom: 1rem; margin-top: 0; }
	& > *:last-child { padding-bottom: 0; }
	.text { overflow: hidden; }
	&:before { content: ''; display: block; position: absolute; top: 0; bottom: 0; left: 0; width: 14rem;	background: url("../img/icon-message-box-info.png") center no-repeat; }
	&:after { content: ''; display: block; position: absolute; left: 14rem; top: 0; bottom: 0; width: 1rem; background: linear-gradient(to right, #f0f0f0aa, transparent); }
}

.message-box.home:before { background-image: url(../img/icon-message-box-home.png); }
.message-box.alert { box-shadow: 0 2px 0 0 #fec127; }
.message-box.alert:before { background-image: url(../img/icon-message-box-alert.png); background-color: #fec127; }
.message-box.percent:before { background-image: url(../img/icon-message-box-percent.png); }
