#bottom {
	@include section;
	background: #f6f6f6; border-top: 20px solid $color-yellow;

	@mixin submenu {
		display: inline-block;
		h2 { font-size: 1.6rem; margin-bottom: 1.5rem; text-transform: uppercase; font-weight: bold;  }
		ul {
			position: relative; display: flex; justify-content: space-between;
			display: grid; grid-auto-rows: min-content; grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
			li { font-size: 1.5rem; margin-bottom: 0.5em; }
			li a { text-decoration: underline; }
			li a:hover { text-decoration: underline; }
		}

		@media screen  and (max-width: 1024px) {
			width: 100% !important;
			ul { display: block; width: 100%; margin-bottom: 4rem; }
			ul li { display: inline-block; width: 250px; }
		}

		@media screen  and (max-width: 640px) {
			text-align: center;
			ul li { width: auto; padding: 0.3rem; }
		}

	}

	.services { @include submenu; width: 25%; }
	.products { @include submenu; width: 74%; }


}