#portfolio {
	@include section();
	background: url(../img/portfolio-bg.jpg) repeat center;

	h2 { @include index-heading; }


	.portfolio-contanier {
	}

	.project-preview {
		text-align: center; position: relative; margin: 1rem;
		img {  display: block; width:100%; }
		.title { height: 6rem; padding: 1rem; border-bottom: 1px solid #a1a1a1; font-size: 1.8rem; font-weight: normal;
			display: flex; align-items: center; justify-content: center; position: relative;
		}
		.title:before { @include null; width: 3rem; height: 2rem; display: inline-block; margin-right: 1rem; background: url(../img/icon-image.svg) no-repeat center / contain; filter: contrast(12%) sepia(100%) brightness(150%) saturate(272%) hue-rotate(8deg); }
		a { position: absolute; top:0; bottom:0; left:0; right: 0; }

		&:hover {
			.title { border-bottom: 4px solid $color-yellow; }
		}

	}
}