#header {
	@include section(2rem);

	& > .container { display: grid; grid-template-columns: repeat(3,auto); grid-template-rows: repeat(3, auto-fit); justify-content: space-between; align-items: center; align-items: center; grid-gap: 2rem; padding-top: 3rem}

	.site-logo  { position: relative; grid-row: 1 / span 2; grid-column: 2; display: inline-block; width:366px; height: 116px; background: url(../img/site-logo.png) center no-repeat; }
	.site-logo a { position: absolute; left:0; right:0; top:0; bottom:0; }
	.address { grid-row: 1; grid-column: 1; max-width: 300px; }
	.social-icons { grid-row: 2; grid-column: 1; }
	.phones {
		grid-row: 1; grid-column: 3; justify-content: flex-end; font-size: 2.4rem; color: $color-yellow; font-weight: normal;
		strong { font-weight: normal; color: $color-black; }
	}
	.worktime {
		grid-row: 2; grid-column: 3; max-width: 300px; justify-self: flex-start; font-size: 1.4rem; color: #222;
		strong { font-weight: normal; color: black; }
	}
	.experience { grid-row: 3; grid-column: 2; justify-self: center; position: relative; display: inline-block; justify-self: center; padding: 0.5rem 0; font-size: 1.3rem; font-style: italic; }

	@mixin with-icon($icon) {
		padding-left: 4rem; position: relative;
		&:before { @include null; width: 3rem; height: 2.5rem; display: inline-block; margin-right: 1rem; background: no-repeat center / contain;
			background-image: url($icon); position: absolute; left:0; top:50%; transform: translateY(-50%);
		}
	}

	.phones { @include with-icon("../img/icon-contacts-phone.svg"); }
	.worktime { @include with-icon("../img/icon-contacts-time.svg"); }
	.address { @include with-icon("../img/icon-contacts-location.svg"); }

	.social-icons a { background-color: #eeeeee; }
	.social-icons a:hover { background-color: $color-black; }

	@mixin line { content: ''; display: block; position: absolute; left:0; right: 0; height: 1px; }
	.experience:before { @include line; top: 0; background: linear-gradient(transparent, #dddcdc, transparent); }
	.experience:after { @include line; bottom:0;  background: linear-gradient(transparent, #dddcdc, transparent); }


	@media screen  and (max-width: 1024px) and (min-width: 640px) {
		& > .container { grid-template-columns: repeat(2, auto);}
		.site-logo { grid-row: 1; grid-column: 1 / span 2; justify-self: center; }
		.address { grid-row : 2; grid-column: 1; justify-self: flex-end; padding-right: 4rem; padding-left: 0; text-align: right; }
		.address:before { right:0; position: absolute; top:50%; left:auto; margin-right: 0;}
		.social-icons { grid-row: 3; grid-column: 1; justify-self: flex-end; }
		.phones { grid-row: 2; grid-column: 2; justify-self: flex-start; }
		.worktime { grid-row: 3; grid-column: 2; justify-self: flex-start; }
		.experience { display: none; }
	}

	@media screen and (max-width: 640px) {
		& > .container { display: flex; flex-flow: column; align-items: center; justify-content: center; }
		& > .container > * {  text-align: left; }
	}
}