#catalog-index {
	@include section;
	background: url(../img/content-bg.jpg) repeat-x center top;

	h2 { @include index-heading-decor; }
	.catalog-container {
		position: relative; display: flex; justify-content: space-between;
		display: grid; grid-auto-rows: min-content; grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
		grid-gap: 2rem; max-width: 1100px; margin: 0 auto;
		.item {
				display: inline-block; text-align: center; position: relative; margin-bottom: 2rem;
				.image { width: 180px; height: 180px; border-radius: 50%; overflow: hidden; position: relative; display: inline-block; opacity: 0.6; margin-bottom: 1rem; transition: .3s all;}
				.image img { display: block; position: absolute; top:0; bottom: 0; left:0; right:0; }
				.title { display: inline-block; height: 4rem; width: auto; padding: 1rem 2rem; display: inline-flex; align-items: center; transition: .3s all; line-height: 1em; }
				.title:before { @include null; width: 2rem; height: 2rem; background: url(../img/icon-decor.svg) no-repeat center / contain; margin-right: 0.5rem;
					filter: contrast(12%) sepia(100%) brightness(150%) saturate(272%) hue-rotate(8deg);
				}
			&:hover {
				.image { opacity: 1; }
				.title { background: $color-yellow; }
				.title:before { filter: none; }
			}
			a { position: absolute; top:0; bottom:0; left:0; right:0; }
		}

		@media screen and (max-width: 1024px) {
			grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
			.item .image { width: 140px; height: 140px; }
		}
	}
}


.v-shop-filters {

	display: grid; grid-gap: 1rem; grid-auto-rows: min-content;
	grid-template-columns: repeat( auto-fit, minmax(100px, 1fr) ); align-items: center; justify-content: stretch;
	padding: 1rem 0 ; border-bottom: 1px solid #e9e9e9; margin-bottom: 2rem; margin-top: -2rem;

	& > * { display: flex; align-items: center; }
	& > * .title { margin-right: 1rem; }

	.sort { flex-grow: 1; grid-area: auto / span 2; }
	.sort select { width: 100%; max-width: 200px;}

	.count {
		select { width: 100%;  max-width: 100px;}
	}

	.search {
		justify-content: flex-end; position: relative;
		input[type="text"] { width: 100%; }
		input[type="submit"] { display: block; width: 3.5rem; height: 3.5rem; position: absolute; right: 3px; top: 50%; transform: translateY(-50%); color: transparent; border-radius: 50%; padding:0; margin:0; background: url(../img/icon-zoom.svg) no-repeat center / auto 2rem , transparent; }
		input[type="submit"]:hover { opacity: 0.5; }
	}

	// Small
	@media screen and (max-width: 640px) {
		display: flex; flex-flow: wrap; grid-gap: 1rem; justify-content: space-between;
		& > * { width: 49%; }
		& > .search { width: 100%; }
	}
}


.v-shop-mini-cart {
	display: flex; align-items: center; min-height: 4rem; justify-content: space-between;
	padding-left: 5rem; background: url(../img/icon-cart.svg) left center no-repeat; background-size: 4rem auto;
	a.open-cart { @include ui-button; padding: 1rem 2rem; margin-left: 1rem; font-size: 1.3rem; }
	.total {
		display: grid; grid-template-columns: repeat(2,auto); grid-template-rows: 1fr; grid-column-gap: 0.5rem; grid-row-gap: 0.1rem;

		.title { grid-area: 1 / 1 / auto / span 2;  }
		.count { grid-area: 2 / 1; display: flex; align-items: center; padding-right: 1.5rem; border-right: 1px solid #ececec; margin-right: 1rem; }
		.price { grid-area: 2 / 2; display: flex; align-items: center; }

		strong { font-weight: normal;  font-size: 1.8rem; margin-right: 0.2em; }
	}
}

@media screen and (max-width: 1280px) {
	#catalog-index .product-preview:nth-child(n+9) { display: none; }
}

@media screen and (max-width: 1060px) {
	#catalog-index .product-preview:nth-child(n+7) { display: none; }
}

@media screen and (max-width: 960px) {
	#catalog-index > .container { padding-top: 0; }
	#catalog-index .v-shop-catalog { margin-top: 0; }
}