#content {
	@include section(4rem);
	& > .container {	display: grid; grid-template-columns: minmax(340px,30%) auto; grid-template-rows: auto; grid-column-gap: 4rem;}

	background: linear-gradient(#f5f5f5, transparent 300px);
}

#side { grid-area: 1 / 1; }
#article { grid-area: 1 / 2; }

#article > .container > * { margin-bottom: 2rem; }

@media screen and (max-width: 960px) {
	#content > .container { display: flex; flex-flow: column;}
	#side > .container { display: block !important; }
	#side .banner { display: none; }
}